import React, { Component } from 'react';
import '../css/header.css';



class Header extends Component {
    render() {
        return (
            <div className="wrapper">
                <div className="container">
                    <nav className="navbar navbar-expand-lg navbar-light ">
                        <a className="navbar-brand" href=""><img className="logo mr-3" src={require('../images/navbar/small-logo.svg')} alt="logo" /></a>
                        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent"
                            aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon"></span>
                        </button>
                        <div className="collapse navbar-collapse" id="navbarSupportedContent">
                            <ul className="navbar-nav">
                                <li className="nav-item mx-4">
                                    <a className="nav-link" href="#">雪場票券</a>
                                </li>
                                <li className="nav-item mx-4">
                                    <a className="nav-link" href="#">找飯店</a>
                                </li>
                                <li className="nav-item mx-4">
                                    <a className="nav-link" href="#">雪具裝備</a>
                                </li>
                                <li className="nav-item mx-4">
                                    <a className="nav-link" href="#">景點排程</a>
                                </li>
                            </ul>
                            <ul className="navbar-nav ml-auto">
                                <li className="nav-item mr-sm-2 my-lg-0">
                                    <input className="navbar-btn btn btn-outline-secondary" type="button" value="登入｜註冊" />
                                    <img className="m-3 navbar-user cursor-point login-show" src={require('../images/navbar/user.svg')}  alt=""/>
                                        <img className="m-3 navbar-message cursor-point login-show" src={require('../images/navbar/message.svg')} alt=""/>
                                            <img className="m-3 navbar-heart cursor-point login-show" src={require('../images/navbar/heart.svg')} alt=""/>
                                                <img className="m-3 navbar-shopping cursor-point" src={require("../images/navbar/shopping-cart.svg")} alt="shopping-cart" />
                                                {/* <!-- user下拉選單 --> */}
                                                 <div className="user-drop-list text-center position-absolute mt-3">
                                                    <ul className="list-unstyled drop-menu">
                                                        <li className="my-3"><a href="" className="nav-link p-0 text-decoration-none">我的帳戶</a></li>
                                                        <li className="my-3"><a href="" className="nav-link p-0 text-decoration-none">我的訂單</a></li>
                                                        <li className="my-3"><a href="" className="nav-link p-0 text-decoration-none">紅利點數</a></li>
                                                        <li className="my-3"><a href="" className="nav-link p-0 text-decoration-none">登出</a></li>
                                                    </ul>
                                                </div>
            </li>
          </ul>
        </div>
      </nav>
                                {/* <!-- ------------------ 光箱start --------------------> */}
      <div className="user-bg-model pt-5">
                                    {/* <!-- 註冊start --> */}
        <div className="user-model-content signup-content text-center p-4">
                                        <div className="close-model">+</div>
                                        <img className="user-small-logo mb-2" src="images/small-logo.svg" alt="" />
                                        <form action="" method="POST">
                                            <div className="user-form-item position-relative my-2 d-flex align-items-center">
                                                <i className="far fa-envelope position-absolute"></i>
                                                <input id="signup-account" className="btn-block p-2 signup-input user-text-input" type="email"
                                                    autoComplete="off" placeholder="電子郵件地址" />
                                            </div>
                                            <small className="text-left user-small-text"></small>
                                            <div className="user-form-item position-relative my-2">
                                                <i className="fas fa-eye-slash position-absolute"></i>
                                                <input id="signup-password" className="btn-block p-2 signup-input user-text-input" type="password"
                                                    autoComplete="off" placeholder="密碼" />
                                            </div>
                                            <small className="text-left user-small-text"></small>
                                            <small className="my-3 user-small-info">我們會透過電子郵件將行銷宣傳、優惠與政策更新資訊發送給您。</small>
                                            <button className="user-orange-btn user-btn signup-btn btn btn-block d-inline-block my-2" type="button">
                                                註冊
                                                    </button>
                                            <small className="my-3 user-small-info text-center"><div className="user-or-line d-inline-block"></div>或<div className="user-or-line d-inline-block"></div></small>
                                            <div className="d-flex justify-content-between align-items-center">
                                                <div className="mx-2 user-third-input py-2 mt-2 d-flex justify-content-around align-items-center">
                                                    <i className="fab fa-facebook-f user-fa-facebook"></i>
                                                    <span className="text-center user-darkblue-text">Facebook</span>
                                                    <span></span>
                                                </div>
                                                <div className="mx-2 user-third-input py-2 mt-2 d-flex justify-content-around align-items-center flex-grow">
                                                    <i className="fab fa-google user-fa-google"></i>
                                                    <span className="user-darkblue-text text-center">Google</span>
                                                    <span></span>
                                                </div>
                                            </div>
                                            <small className="position-absolute login-text toggle-login"><a href=""
                                                className="user-darkblue-text text-decoration-none">我要登入</a></small>
                                        </form>
                                    </div>
                                    {/* <!-- 註冊成功start --> */}
        <div className="user-model-content signup-success text-center p-4">
                                        <div className="close-model">+</div>
                                        <img className="user-small-logo mb-3" src="images/small-logo.svg" alt="" />
                                        <h3 className="my-5">註冊成功！</h3>
                                        <h5 className="px-3">
                                            我們已經將一封認證信寄至您的信箱，請點擊信裡的確認連結已開通帳號。
          </h5>
                                        <button className="user-btn user-orange-btn my-4 btn btn-block d-inline-block my-2" type="button">
                                            重寄認證信給我
          </button>
                                    </div>
                                    {/* <!-- 註冊成功end --> */}
        {/* <!-- 註冊end --> */}
        {/* <!-- 登入start --> */}
        <div className="user-model-content login-content text-center p-4">
                                        <div className="close-model">+</div>
                                        <img className="user-small-logo mb-2" src="images/small-logo.svg" alt="" />
                                        <form action="" method="POST">
                                            <div className="user-form-item position-relative my-2 d-flex align-items-center">
                                                <i className="far fa-envelope position-absolute"></i>
                                                <input id="login-account" className="btn-block p-2 login-input user-text-input" type="email" autoComplete="off"
                                                    placeholder="電子郵件地址" />
                                            </div>
                                            <small className="text-left user-small-text"></small>
                                            <div className="user-form-item position-relative my-2">
                                                <i className="fas fa-eye-slash position-absolute"></i>
                                                <input id="login-password" className="btn-block p-2 login-input user-text-input" type="password"
                                                    autoComplete="off" placeholder="密碼" />
                                            </div>
                                            <small className="text-left user-small-text"></small>
                                            <div className="d-flex p-0 justify-content-between align-items-center mt-3 mb-1">
                                                <div className="">
                                                    <input type="checkbox" name="checkbox" />
                                                    <label className="user-darkblue-text" htmlFor="checkbox">記住我</label>
                                                </div>
                                                <small className=""><a href="" className="forget-pwd text-decoration-none">忘記密碼</a></small>
                                            </div>
                                            <button className="user-btn user-blue-btn login-btn my-1 btn btn-block d-inline-block" type="button">
                                                登入
            </button>
                                            <small className="my-3 user-small-info text-center"><div className="user-or-line d-inline-block"></div>或<div className="user-or-line d-inline-block"></div></small>
                                            <div className="d-flex justify-content-between align-items-center">
                                                <div className="mx-2 user-third-input py-2 mt-2 d-flex justify-content-around align-items-center">
                                                    <i className="fab fa-facebook-f user-fa-facebook"></i>
                                                    <span className="text-center user-darkblue-text">Facebook</span>
                                                    <span></span>
                                                </div>
                                                <div className="mx-2 user-third-input py-2 mt-2 d-flex justify-content-around align-items-center flex-grow">
                                                    <i className="fab fa-google user-fa-google"></i>
                                                    <span className="text-center user-darkblue-text">Google</span>
                                                    <span></span>
                                                </div>
                                            </div>
                                            <small className="position-absolute login-text toggle-login"><a href=""
                                                className="user-darkblue-text text-decoration-none">我要註冊</a></small>
                                        </form>
                                    </div>
                                    {/* <!-- 登入end --> */}
        {/* <!-- 忘記密碼start --> */}
        <div className="user-model-content text-center p-4 forget-content">
                                        <div className="close-model">+</div>
                                        <img className="user-small-logo mb-3" src="images/small-logo.svg" alt="" />
                                        <h3 className="my-5">忘記密碼？</h3>
                                        <div className="user-form-item">
                                            <input id="forget-pwd-input" className="btn-block p-2 user-text-input" type="text" autoComplete="off"
                                                placeholder="請填寫註冊時使用的email" />
                                        </div>
                                        <small className="text-left user-small-text"></small>
                                        <button className="user-btn user-blue-btn resend-pwd my-4 btn btn-block d-inline-block my-2" type="button">
                                            重寄認證信給我
          </button>
                                        <small><a href="" className="user-back-login text-decoration-none">回上一頁</a></small>
                                    </div>
                                    {/* <!-- 忘記密碼end --> */}
      </div>
    </div>
                            {/* <!-------------------- 光箱over --------------------> */}
  </div>
                        );
                    }
                }
                
export default Header;