import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel';
 
class ASlider extends Component {
    render() {
        return (
            <div style={{width:'500px',height:'500px',margin:'auto'}}>
                <Carousel autoPlay>
                <div>
                    <img src={require('../images/AttractionsImg/Naruto-1.JPG')} />
                    <p className="legend">Legend 1</p>
                </div>
                <div>
                    <img src={require('../images/AttractionsImg/Naruto-2.jpg')} />
                    <p className="legend">Legend 2</p>
                </div>
                <div>
                    <img src={require('../images/AttractionsImg/Naruto-3.jpg')} />
                    <p className="legend">Legend 3</p>
                </div>
            </Carousel>
            </div>
            
        );
    }
};
 
export default ASlider;